const Sliders = (function () {
   "use strict";
   const reviewsSlider = $('.js-reviews-slider');
   return {
      initReviewsSlider: function(){
         reviewsSlider.slick({
            dots: true,
            infinite: false,
            speed: 500,
            variableWidth: true,
            cssEase: "linear",
            swipeToSlide: true,
            arrows: true,
            responsive: [
              {
                breakpoint: 1025,
                settings: {
                  variableWidth: false,
                  slidesToShow: 2,
                  slidesToScroll: 1,
                  adaptiveHeight: true
                }
              },
              {
                breakpoint: 901,
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                  adaptiveHeight: true,
                  variableWidth: false,
                }
              }
            ]
          });
      },
     init: function () {
      Sliders.initReviewsSlider();
     }
   };
 })();
 
 export default Sliders;