import Parallax from "../libs/parallax.js";
const Gallery = (function () {
	 "use strict";
	 const reportItem = $('.report');
	 const imageGallery = $(".wrap-img");
	 const btnShowInfo = $('.js-show-info');
	 const btnMap = $('.js-show-gallery');
	 function loadLazyImages(imgs) {
		 for (let i = 0; i < imgs.length; i++) {
			 const image = $(imgs[i]);
			 const realSrc =image.data('lazy-src');
			 image.attr("src", realSrc);
		 }
	 }
	 return {
			showListPhotos: function (){
				const lazyImgs = $(`[data-lazy-src]`);
				loadLazyImages(lazyImgs);
			},
			showGallery: function (){
				btnMap.click(function (e) { 
					e.preventDefault();
					const _this = $(this);
					const href = _this.attr("href");
					if (href.length <= 1) return;
					const target = $(href);
					if (!target.length) return;
					const top = target.offset().top;
					reportItem.removeClass('active');

					Gallery.showListPhotos();
					target.addClass('active');
					target.addClass('show-slider');
					Gallery.destroySlick();
					Gallery.initSliderGallery();
					$("html, body").animate({scrollTop: top},800);
				});
			},
			animateGallery: function () {
				btnShowInfo.click(function (e) {
					e.preventDefault();
					const _this = $(this);
					const parent = _this.parents('.report');

					Gallery.showListPhotos();

					reportItem.removeClass('active');

					parent.removeClass("show-slider");
					parent.addClass("animate");
					parent.addClass('active');

					Gallery.destroySlick();
					Gallery.initSliderGallery();
				 });
				 $('.report__close').click(function (e) {
					 e.preventDefault();
					 const _this = $(this);
					 const parent = _this.parents('.report');
					 parent.removeClass('active');
					 parent.removeClass("show-slider");
					 parent.addClass("animate");
				 });
				 imageGallery.click(function () {
					 const _this = $(this);
					 const slidePhoto = _this.find("img").data("scr");
					 const parent = _this.parents('.report');
					 parent.removeClass("animate");
					 parent.addClass('show-slider');
					 setTimeout(() => {
						 $(".slider").fadeIn(500);
						 $(".slider-for").slick("slickGoTo", slidePhoto);
					 }, 0);
				 });
			},
			initSliderGallery: function () {
				 $(".slider-for").slick({
					 slidesToShow: 1,
					 slidesToScroll: 1,
					 arrows: true,
					 fade: true,
					 asNavFor: ".slider-nav"
				 });
				 $(".slider-nav").slick({
					 slidesToShow: 13,
					 slidesToScroll: 1,
					 asNavFor: ".slider-for",
					 dots: false,
					 arrows: false,
					 centerMode: true,
					 focusOnSelect: true,
					 responsive: [
						 {
							 breakpoint: 1681,
							 settings: {
								 slidesToShow: 10
							 }
						 },
						 {
							 breakpoint: 1281,
							 settings: {
								 slidesToShow: 8
							 }
						 },
						 {
							 breakpoint: 1025,
							 settings: {
								 slidesToShow: 7
							 }
						 },
						 {
							 breakpoint: 901,
							 settings: {
								 slidesToShow: 6
							 }
						 },
						 {
							 breakpoint: 768,
							 settings: {
								 slidesToShow: 5
							 }
						 },
						 {
							 breakpoint: 641,
							 settings: {
								 slidesToShow: 4
							 }
						 },
						 {
							 breakpoint: 481,
							 settings: {
								 slidesToShow: 3
							 }
						 }
					 ]
				 });
			},
			destroySlick: function () {
				$(".slider-for").slick("unslick");
				$(".slider-nav").slick("unslick");
			},
			initParallax: function (id){
				const scene = document.getElementById(id);
				new Parallax(scene);
			},
		 init: function () {
			Gallery.animateGallery();
			if ($(window).width() < 900) {
				$(".slider").fadeIn(500);
			} else {
				$(".slider").fadeOut(500);
			}
			Gallery.initSliderGallery();
			Gallery.showGallery();
			Gallery.initParallax('scene1');
			Gallery.initParallax('scene2');
			Gallery.initParallax('scene3');
			Gallery.initParallax('scene4');
			Gallery.initParallax('scene5');
			Gallery.initParallax('scene6');
			Gallery.initParallax('scene7');
		 }
	 };
 })();
 
 export default Gallery;