import noScroll from "../libs/no-scroll";
const Menu = (function () {
   "use strict";
   const links = $('.js-scroll');
   const burgerMenu = $('.js-burger');
   const navMenuMobile = $('#js-mobile-menu');
   const blockForHide = $('.js-hide-content');
   return {
      scrollToTarget: function(){
         links.click(function (e) {
            e.preventDefault();
            const _this = $(this);
            const href = _this.attr("href");
            if (href.length <= 1) return;
            const target = $(href);
            if (!target.length) return;
            const top = target.offset().top;
            burgerMenu.removeClass('active');
            navMenuMobile.removeClass('active');
            blockForHide.removeClass('hide');
            noScroll.off();
            $("html, body").animate(
               {
                  scrollTop: top
               },
               800
            );
         });
      },
      showMobileMenu: function(){
         burgerMenu.click(function (e) { 
            e.preventDefault();
            const _this = $(this);
            _this.toggleClass('active');
            navMenuMobile.toggleClass('active');
            blockForHide.toggleClass('hide');
            noScroll.toggle();
         });
      },

     init: function () {
      Menu.scrollToTarget();
      Menu.showMobileMenu();
     }
   };
 })();
 
 export default Menu;